import React from 'react';
import FadeLink from '../common/fadeLink';
import formatLink from '../common/formatLink';

const WhatWeDid = ({ disciplines, categories, externalLink }) => (
  disciplines.length > 0 ? (
    <div className="article__contain">
      <div className="article__whatWeDid">
        <p className="article__whatWeDid-title fontSize-p3 fontSize-uppercase">What we did</p>
        <div className="article__whatWeDid-contain">
          <div className="article__whatWeDid-disciplines">
            <ul className="article__whatWeDid-disciplines-list fontSize-p1">
              {disciplines.map((discipline, i) => (
                <li
                  key={discipline}
                  className="article__whatWeDid-disciplines-list-item fontSize-p1"
                >
                  <FadeLink
                    className="article__whatWeDid-disciplines-link"
                    to={`/work/${formatLink(discipline)}`}
                  >
                    {discipline}
                  </FadeLink>
                </li>
              ))}

              {categories.map((category, i) => (
                <li
                  key={category}
                  className="article__whatWeDid-disciplines-list-item fontSize-p1"
                >
                  <FadeLink
                    className="article__whatWeDid-disciplines-link"
                    to={`/work/${formatLink(category)}`}
                  >
                    {category}
                  </FadeLink>
                </li>
              ))}
            </ul>
          </div>
          <div className="article__whatWeDid-seeMore">
            {externalLink && (
              <a
                href={externalLink}
                target="_blank"
                rel="noopener noreferrer"
                className="article__whatWeDid-seeMore-link fontSize-p2"
              >
                See more here
              </a>
            )}
          </div>
        </div>
      </div>
    </div>
  ) : null
);

export default WhatWeDid;
