import { gsap, Circ } from 'gsap';

const timings = [0, 0.6, 0.4];

const relatedArticlesTransition = async (articleTransitionEl, direction, e, exit) => {
  const animationStages = [];
  const target = e.target;
  const tileDimensions = target.getBoundingClientRect();
  const heroEl = exit.node.querySelector('.article__hero');
  const heroDimensions = heroEl.getBoundingClientRect();

  let moveTo = 0;

  if (heroDimensions.width < 768) {
    moveTo = window.pageYOffset + heroDimensions.top;
  }

  let imgClone = target.cloneNode(true);

  if (imgClone === undefined) {
    const clicked = exit.node.querySelector(`.article__related-option.-${direction}`);

    imgClone = clicked.querySelector('img').cloneNode(true);
  }

  imgClone.removeAttribute('loading');
  imgClone.removeAttribute('srcset');

  animationStages.push(
    async () =>
      new Promise(resolve => {
        gsap.set(articleTransitionEl.current, { clearProps: 'all' });

        const copyTileStyle = {
          y: tileDimensions.y + 'px',
          x: tileDimensions.x + 'px',
          width: tileDimensions.width + 'px',
          height: tileDimensions.height + 'px',
          opacity: 1,
        };

        const imgLoaded = () => {
          gsap.to(articleTransitionEl.current, timings[0], {
            ...copyTileStyle,
            onComplete: () => {
              articleTransitionEl.current.appendChild(imgClone);
              exit.node.querySelector('.article').classList.add('-fade');

              requestAnimationFrame(resolve);
            },
          });
        };

        if (imgClone.complete) {
          imgLoaded();
        } else {
          imgClone.addEventListener('load', () => {
            imgLoaded();
          });
        }
      })
  );

  animationStages.push(
    async () =>
      new Promise(resolve => {
        const scale = heroDimensions.width / tileDimensions.width;
        const growToHero = {
          x: heroDimensions.x + 'px',
          scale: scale,
          height: heroDimensions.height / scale + 'px',
          ease: Circ.easeOut,
        };

        // console.log('growToHero',growToHero);

        gsap.to(articleTransitionEl.current, timings[1], {
          ...growToHero,
          onComplete: () => requestAnimationFrame(resolve),
        });
      })
  );

  animationStages.push(
    async () =>
      new Promise(resolve => {
        window.scrollTo(0, 0);
        imgClone.style.filter = 'blur(3px)';

        gsap.to(articleTransitionEl.current, timings[2], {
          y: moveTo + 'px',
          ease: Circ.easeOut,
          onComplete: () => {
            requestAnimationFrame(resolve);
          },
        });
      })
  );

  for (const animation of animationStages) {
    await animation();
  }
};

export default relatedArticlesTransition;
