import { gsap } from 'gsap';

const timing = 500;

let newTransitionEl, heroMedia;

const newArticleFadeIn = articleTransitionEl => {
  newTransitionEl.style.left = '50%';
  newTransitionEl.style.transform = 'translate(-50%, 0px)';
  newTransitionEl.style.transition = 'unset';
  newTransitionEl.style.opacity = 1;

  setTimeout(() => {
    if(articleTransitionEl.current !== null){
      articleTransitionEl.current.style.transition = 'unset';
      articleTransitionEl.current.style.opacity = 0;
    }
  }, 100);

  setTimeout(() => {
    const heroImg = heroMedia.querySelector('img');
    newTransitionEl.style.position = 'absolute';
    heroMedia.style.opacity = 1;

    const heroImgLoaded = () => {
      setTimeout(() => {
        newTransitionEl.style.transition = '';
        newTransitionEl.style.opacity = 0;
      }, 100);

      setTimeout(() => {
        gsap.set(newTransitionEl, { clearProps: 'all' });
        newTransitionEl.innerHTML = '';
      }, timing);
    };

    if (heroImg.complete) {
      heroImgLoaded();
    } else {
      heroImg.addEventListener('load', () => {
        heroImgLoaded();
      });
    }
  }, 50); // give FF 50ms to add hero img
};

export default newArticleFadeIn;

export const prepareEntryPage = async (pages, articleTransitionEl) => {
  const entry = await pages.entry;
  const articleEl = entry.node.querySelector('.article');
  const transitionImgClone = articleTransitionEl.current.lastChild.cloneNode(true);

  heroMedia = articleEl.querySelector('.article__hero-media');
  newTransitionEl = articleEl.querySelector('.article__movingOverlay');

  articleEl.classList.add('-articleTransition');

  for (const placholders of heroMedia.querySelectorAll('div')) {
    placholders.style.backgroundColor = 'unset';
  }

  heroMedia.style.opacity = 0;

  gsap.set(newTransitionEl, { y: 0, opacity: 0, transition: 'unset' });
  newTransitionEl.appendChild(transitionImgClone);

  return entry;
};
