import React, { useRef } from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import Img from 'gatsby-image';
import TransitionLink from 'gatsby-plugin-transition-link';

import getRelatedArticles from './getRelatedArticles';
import relatedArticlesTransition from './relatedArticlesTransition';
import newArticleFadeIn, { prepareEntryPage } from './newArticleFadeIn';
import fluidImgProps from '../common/fluidImgProps';
import CursorOverlay from '../common/cursorOverlay';

const RelatedArticles = ({ currArticle, data }) => {
  const getAllArticles = useStaticQuery(graphql`
  {
    allSanityProject {
      nodes {
        id
        slug{
          current
        }
        heroImage {
          heroDescription
          asset {
            url
            fluid(maxWidth: 600) {
              src
              sizes
              srcSet
              srcWebp
              srcSetWebp
              aspectRatio
            }
            metadata {
              palette {
                dominant {
                  background
                }
              }
            }
          }
        }
        disciplines
        categories
      }
    }
  }
`);

  if (data === undefined) {
    data = getAllArticles;
  }

  const allArticles = data.allSanityProject.nodes;
  const related = getRelatedArticles(currArticle, allArticles);
  const articleTransitionEl = useRef();

  return (
    <>
      <div className="article__contain">
        {related ? (
          <div className="article__related">
            <div className="article__related-title">
              <p className="article__related-title-text fontSize-p3 fontSize-uppercase">
                Related Work
              </p>
            </div>

            <div className="article__related-links">
              {related.map((article, i) => {
                const props = fluidImgProps(article.heroImage.asset);
                const direction = i === 0 ? 'previous' : 'next';

                let e;

                return (
                  <TransitionLink
                    exit={{
                      zIndex: 2,
                      length: 3,
                    }}
                    entry={{
                      delay: 1.1,
                      length: 0,
                    }}
                    onClick={event => (e = event)}
                    trigger={async pages => {
                      // wait until we have access to both pages
                      const exit = await pages.exit;

                      await Promise.all([
                        prepareEntryPage(pages, articleTransitionEl),
                        relatedArticlesTransition(articleTransitionEl, direction, e, exit),
                      ]);

                      newArticleFadeIn(articleTransitionEl);
                    }}
                    key={article.slug.current}
                    to={'/work/articles/' + article.slug.current}
                  >
                    <CursorOverlay>
                      <Img className={`article__related-option -${direction}`} {...props} />
                    </CursorOverlay>
                  </TransitionLink>
                );
              })}
            </div>
          </div>
        ) : null}
      </div>
      <div className="article__movingOverlay" ref={articleTransitionEl} />
    </>
  );
};

export default RelatedArticles;
