import React from 'react';
import VimeoEl from '../common/vimeo';

export const urlToId = url => {
  const vimeoSplit = url.split('/');

  let id = vimeoSplit[vimeoSplit.length - 1]; // last value in url

  id = id.split('?')[0]; // remove queryStrings

  if (!/^\d+$/.test(id)) {
    // id there was a trailing backslash
    id = vimeoSplit[vimeoSplit.length - 2];
  }

  return id;
};

const Vimeo = ({ ambient, url, iconColor }) => {
  const id = urlToId(url);

  let vimeoOptions = {
    id,
    loop: true,
    background: ambient ? true : false,
  };

  return (
    <div className="row article-media">
      <VimeoEl
        className={'vimeo__video aspect__16-by-9'}
        vimeoOptions={vimeoOptions}
        soundIcon={iconColor}
      />
    </div>
  );
};

export default Vimeo;
