import React from 'react';
import { graphql } from 'gatsby';

import Hero from '../components/article/hero';
import Heading from '../components/article/heading';
import Modules from '../components/article/articleModules';
import WhatWeDid from '../components/article/whatWeDid';
import RelatedArticles from '../components/article/relatedArticles';

export default React.memo(({ data }) => {
  const entry = data.allSanityProject.nodes[0];

  return (
    <section className="article">
      <Hero heroImage={entry.heroImage.asset} heroBannerVideo={entry.heroImage.heroVideo} />

      <div className="article__contain">
        <Heading title={entry.title} text={entry.heroImage.heroDescription} />

        <Modules moduleData={entry.modules} />
      </div>

      <WhatWeDid {...entry} />

      <RelatedArticles currArticle={entry} />
    </section>
  );
});

export const query = graphql`
  query ($slug: String!) {
    allSanityProject(filter: {slug: {current: {eq: $slug}}}) {
      nodes {
        title
        heroImage {
          heroDescription
          heroVideo
          asset {
            url
            fluid(maxWidth: 1920) {
              src
              sizes
              srcSet
              srcWebp
              srcSetWebp
              aspectRatio
            }
            metadata {
              palette {
                dominant {
                  background
                }
              }
            }
          }
        }
        
        modules {
          ...ModuleData
        }

        disciplines
        categories
        externalLink

        relatedProjects{
          id
          slug{
            current
          }
          heroImage {
            asset {
              url
              fluid(maxWidth: 1920) {
                src
                sizes
                srcSet
                srcWebp
                srcSetWebp
                aspectRatio
              }
              metadata {
                palette {
                  dominant {
                    background
                  }
                }
              }
            }
          }
        }
      }
    }
  }
`;
