import React, { useEffect } from 'react';
import { gsap, Power2 } from 'gsap';

const checkDocument = typeof document === `undefined` ? false : true; // gatsby cannot have window called while building the pages

let cursorOverlay, cursorOverlayCursor, cursorDimensions, timeout;

const preventRerender = (prevProps, nextProps) => {
  if (prevProps.childState !== nextProps.childState) {
    return false;
  }

  return true;
};

const CursorOverlay = React.memo(({ children, clickable = true }) => {
  const hoverItem = React.createRef();

  if (checkDocument && (cursorOverlay === undefined || cursorOverlayCursor === undefined)) {
    cursorOverlay = document.querySelector('.cursorOverlay');
    if (cursorOverlay === null) return null; // for unit tests

    cursorOverlayCursor = cursorOverlay.querySelector('.cursorOverlay-cursor');
    cursorDimensions = cursorOverlay.getBoundingClientRect();
  }

  useEffect(() => {
    hoverItem.current.addEventListener('mouseenter', onMouseEnter);
    hoverItem.current.addEventListener('mouseleave', onMouseLeave);
    hoverItem.current.addEventListener('mousemove', onMouseMove);
    hoverItem.current.addEventListener('dragstart', onDragstart);

    if (clickable) {
      hoverItem.current.addEventListener('click', onClick);
    }

    const { current } = hoverItem;

    return () => {
      current.removeEventListener('mouseenter', onMouseEnter);
      current.removeEventListener('mouseleave', onMouseLeave);
      current.removeEventListener('mousemove', onMouseMove);
      current.removeEventListener('dragstart', onDragstart);

      if (clickable) {
        current.removeEventListener('click', onClick);
      }
    };
  }, [hoverItem, clickable]);

  return (
    <div ref={hoverItem} className="hover-cursor">
      {children}
    </div>
  );
}, preventRerender);

export default CursorOverlay;

const onMouseEnter = () => {
  cursorOverlay.classList.remove('-hidden');
  cursorOverlayCursor.style.transform = 'scale(0.4)';
};

const onMouseLeave = () => {
  if (cursorOverlay === null) return;
  cursorOverlay.classList.add('-hidden');
  cursorOverlayCursor.style.transform = 'scale(0)';
};

const onMouseMove = e => {
  if (timeout === false) {
    onMouseEnter();
  }

  // console.log('e',e);
  if (cursorOverlay === null) return;
  let x = e.clientX - cursorDimensions.width / 2;
  let y = e.clientY - cursorDimensions.height / 2;

  cursorOverlay.style.transform = `translate(${x}px, ${y}px)`;

  clearTimeout(timeout);
  timeout = setTimeout(() => {
    cursorOverlay.classList.add('-hidden');
    cursorOverlayCursor.style.transform = 'scale(0)';
    timeout = false;
  }, 1000);
};

const onClick = async () => {
  const rippleTime = 0.6;
  const easing = Power2.easeOut;

  gsap.set(cursorOverlayCursor, {
    scale: 0.1,
    opacity: 0,
    transition: 'none',
  });

  gsap.set(cursorOverlay, { transition: 'none' });

  await gsap.to(cursorOverlayCursor, rippleTime * 0.3, {
    scale: 1,
    opacity: 0.5,
    ease: easing,
  });

  await gsap.to(cursorOverlayCursor, rippleTime * 0.2, {
    scale: 1,
    opacity: 0.7,
    ease: easing,
  });

  await gsap.to(cursorOverlayCursor, rippleTime * 0.2, {
    scale: 0.1,
    opacity: 0,
    ease: easing,
  });

  cursorOverlay.classList.add('-hidden');
  cursorOverlayCursor.removeAttribute('style');
};

const onDragstart = () => {
  return false;
};
