import React, { useState} from 'react';
import Img from 'gatsby-image';

import VimeoEl from '../common/vimeo';
import fluidImgProps from '../common/fluidImgProps';

const checkWindow = typeof window === `undefined` ? false : true; // gatsby cannot have window called while building the pages

const Hero = React.memo(({ heroBannerVideo, heroImage }) => {
  const vimeoUrl = heroBannerVideo !== null ? heroBannerVideo : false;
  const imgProps = fluidImgProps(heroImage);

  if(checkWindow && !!navigator.platform && /iPhone|iPod/.test(navigator.platform)){
    reduceImgSrc(imgProps);
  }

  return <Content imgProps={imgProps} vimeoUrl={vimeoUrl} />;
});

export default Hero;

const reduceImgSrc = imgProps => {
    let smallImg = imgProps.fluid.srcSet.split(',')[1];
    if(smallImg === undefined) return;
    smallImg = smallImg.split(' ')[0];

    imgProps.fluid.src = smallImg;
    imgProps.fluid.srcSet = smallImg;
}

const Content = ({imgProps, vimeoUrl}) => {
  const [imgLoaded, updateImgLoaded] = useState(false);
  const [videoLoaded, updateVideoLoaded] = useState(false);

  return (
    <div className="article__hero">
      <Img
        className={`
          article__hero-media 
          ${!imgLoaded ? '-zeroWidth' : ''}
          ${videoLoaded ? '-hidden' : ''}
        `}
        // unset inline styles for '.-zeroWidth' css transition
        imgStyle={{
          top: '',
          left: '',
          transform: '',
          transition: '',
          width: '',
          height: '',
        }}
        {...imgProps}
        onLoad={() => updateImgLoaded(true)}
        loading="eager"
      />

      {vimeoUrl && (
        <VimeoEl
          className={`vimeo__video article__hero-vimeo`}
          vimeoOptions={{ id: vimeoUrl, loop: true, background: true }}
          style={{ position: 'absolute' }}
          videoPlayingCallback={() => updateVideoLoaded(true)}
        />
      )}
    </div>
  )
}