import React from 'react';
import { graphql } from 'gatsby';

import Paragraph from './paragraph';
import Image from './image';
// import ImageSequence from './imageSequence';
import Vimeo from './vimeo';

const ArticleModules = React.memo(({ moduleData }) => (
  <>
    {moduleData.map((item, i) => {
      if (item.__typename === 'SanityParagraph') {
        return <Paragraph key={`module-${i}`} text={item.paragraph} />;
      }

      if (item.__typename === 'SanityModuleImage') {
        return <Image key={`module-${i}`} img={item.asset.asset} />;
      }

      // TODO - add sequence
      // if (item.moduleImageSequence && item.moduleImageSequence.length > 0) {
      //   return <ImageSequence key={`module-${i}`} imgs={item.moduleImageSequence} />;
      // }

      if (item.__typename === 'SanityVimeo') {
        return <Vimeo key={`module-${i}`} {...item} />;
      }

      return null;
    })}
  </>
));

export default ArticleModules;

export const query = graphql`
  fragment ModuleData on SanityImageSequenceOrModuleImageOrParagraphOrVimeo {
    ... on SanityModuleImage {
        asset {
          asset {
            fluid(maxWidth: 1100) {
              src
              sizes
              srcSet
              srcWebp
              srcSetWebp
              aspectRatio
            }
            metadata {
              palette {
                dominant {
                  background
                }
              }
            }
          }
        }
      }
      ... on SanityParagraph {
        paragraph
      }
      ... on SanityVimeo {
        ambient
        iconColor
        url
      }
  }
`;
