import Cookies from 'universal-cookie';

const cookies = new Cookies();

const filterArticles = (allEntries, filter) => {
  let entries = checkCookie([...allEntries]);

  if (filter !== undefined) {
    entries = entries.filter(({ disciplines }) => {
      return disciplines.some(discipline => discipline === filter);
    });
  }

  return entries;
};

export default filterArticles;

export const checkCookie = array => {
  let checkCookie = cookies.get('dwTiles');

  if (checkCookie === undefined) {
    const randomIndices = shuffleArray(array);

    // set cookie so tiles only randomised once a day
    let tomorrow = new Date();
    tomorrow.setDate(tomorrow.getDate() + 1);

    cookies.set('dwTiles', JSON.stringify(randomIndices), {
      expires: tomorrow,
    });
  } else {
    array = checkCookie.map(i => array[i]);
  }

  return array;
}

export const shuffleArray = array => {
  let currentIndex = array.length;
  let temporaryValue;
  let randomIndex;

  array.forEach((item, i) => {
    item.index = i;
  });

  // While there remain elements to shuffle...
  while (0 !== currentIndex) {
    // Pick a remaining element...
    randomIndex = Math.floor(Math.random() * currentIndex);
    currentIndex -= 1;

    // And swap it with the current element.
    temporaryValue = array[currentIndex];
    array[currentIndex] = array[randomIndex];
    array[randomIndex] = temporaryValue;
  }

  const randomIndices = array.map(val => val.index);

  return randomIndices;
};
